import { Component, OnInit } from '@angular/core';
import { UserDetailsService } from '../user-details.service';
import { Router, ActivatedRoute } from '@angular/router';
@Component({
  selector: 'app-user-detalis',
  templateUrl: './user-detalis.component.html',
  styleUrls: ['./user-detalis.component.css']
})
export class UserDetalisComponent implements OnInit {
private userDetail:any;
sub:any;
id:any;
  constructor(private userService:UserDetailsService,private route:ActivatedRoute) { }

  ngOnInit() {
	  
	  this.sub = this.route.params.subscribe(params =>{
		  this.id = params['id'];
	  });
	  this.employeeDetails()
  }
  
  
	employeeDetails(){
		this.userService.get_employee(this.id).subscribe((res: any[])=>{
			this.userDetail = res;
		})
	}
}
