import { Component, OnInit } from '@angular/core';
import { regReq } from '../models/login';
import { Router } from '@angular/router';
import { FormBuilder, FormGroup, Validators,FormControl } from '@angular/forms';
import { ToastrService } from 'ngx-toastr';
@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {

registerform:FormGroup;
submitted= false;
name:any;
email:any;
pass:any;
phone:any;
//for register variable data

//Question if there is no use so why we should call this variable sir

regReqData: regReq;

  constructor(private router:Router,private formBuilder: FormBuilder,private toastr: ToastrService) { }

  ngOnInit() {
	  
	  this.regReqData = new regReq();
	  
	  this.registerform = this.formBuilder.group({
      name: ['', Validators.required],
      email: ['', [Validators.required, Validators.email]],
      pass: ['', [Validators.required, Validators.minLength(6)]],
	  phone: ['', Validators.required]
    });
  }

register(){
	this.submitted = true;
	if(this.registerform.invalid){	
	this.toastr.error('Please fill the all fields', 'Register form');
	return;
	}else{localStorage.setItem("name", this.regReqData.name);
	localStorage.setItem("email", this.regReqData.email);
	localStorage.setItem("pass", this.regReqData.pass);
	localStorage.setItem("phone", this.regReqData.phone);
	
	//for success message
	this.toastr.success('Register Success', 'Register Form');
		//alert("success Full login ---");
	   //navigate to login page
	   this.router.navigate(['/login']);
	}
}


}
