import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule }    from '@angular/forms';
import { AppComponent } from './app.component';
import { AppRoutingModule } from './app-routing.module';
import { RouterModule, Routes } from '@angular/router';
import { LoginHeaderComponent } from './login-header/login-header.component';
import { LoginComponent } from './login/login.component';
import { RegisterComponent } from './register/register.component';
import { ChangePasswordComponent } from './change-password/change-password.component';
import { DashboardHeaderComponent } from './dashboard-header/dashboard-header.component';
import { ContactComponent } from './contact/contact.component';
import { EmployeeDetailsComponent } from './employee-details/employee-details.component';
import { UserDetalisComponent } from './user-detalis/user-detalis.component';
import { DashboardComponent } from './dashboard/dashboard.component';
import { FooterComponent } from './footer/footer.component';
import { HttpClientModule,HttpClient  } from '@angular/common/http';
import { UserDetailsService } from './user-details.service';
import { EmployeesFilterPipe } from './employees-filter.pipe';

import { NgxPaginationModule } from 'ngx-pagination';
import { NgMultiSelectDropDownModule } from 'ng-multiselect-dropdown';
import { NgxSpinnerModule } from 'ngx-spinner';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations'; 
import { ToastrModule } from 'ngx-toastr';
import { AuthGuardGuard } from './auth-guard.guard';

import { AgGridModule } from 'ag-grid-angular';


@NgModule({
  declarations: [
    AppComponent,
    LoginHeaderComponent,
    LoginComponent,
    RegisterComponent,
    ChangePasswordComponent,
    DashboardHeaderComponent,
    ContactComponent,
    EmployeeDetailsComponent,
    UserDetalisComponent,
    DashboardComponent,
    FooterComponent,
    EmployeesFilterPipe
  ],
  imports: [
    BrowserModule,
    AppRoutingModule,
	HttpClientModule,
	FormsModule,ReactiveFormsModule,
	NgxSpinnerModule,
	NgxPaginationModule,
	BrowserAnimationsModule,
    ToastrModule.forRoot(),
	NgMultiSelectDropDownModule.forRoot(),
	AgGridModule.withComponents([AppComponent]),
	RouterModule.forRoot([
	{path:'',
	    component:LoginHeaderComponent,
		children:[
		{path:'register', component:RegisterComponent},
		{path:'login', component:LoginComponent},
		{path:'changepass', component:ChangePasswordComponent}
		]
	},
	{path:'',
	    component:DashboardHeaderComponent,
		children:[
		{path:'contact', component:ContactComponent},
		{path:'dashboard', component:DashboardComponent},
		{path:'employees', component:EmployeeDetailsComponent},
		{path:'userDetail/:id', component:UserDetalisComponent}
		]
	},
	
	//redirect default page here
	{path:'**', redirectTo:'register'}
	])
  ],
  providers: [UserDetailsService, AuthGuardGuard],
  bootstrap: [AppComponent]
})
export class AppModule { }
