import { Component, OnInit } from '@angular/core';
import { UserDetailsService } from '../user-details.service';
import { Router } from '@angular/router';
import { NgxSpinnerService } from 'ngx-spinner';
@Component({
  selector: 'app-employee-details',
  templateUrl: './employee-details.component.html',
  styleUrls: ['./employee-details.component.css']
})
export class EmployeeDetailsComponent implements OnInit {
	
	//loading
	loading:boolean;
	
	//selected rows variables
	row:any;
	rowindex:any;
	showModal:boolean = false;
	
 emplyIndex:any;
 private employess = [];
showDetails:boolean = false;
  constructor(private userService: UserDetailsService,public router:Router, private spinner: NgxSpinnerService) { }

  ngOnInit() {
	  this.loading = true;
	  this.get_employess();
	  
	  //selected row
	  this.row = [];
  }
  
  //get all employees
	get_employess(){
		//alert("hi");
		/** spinner starts on init */
    //this.spinner.show();
 
    setTimeout(() => {
        /** spinner ends after 5 seconds */
        this.spinner.hide();
		}, 5000);  
		this.showDetails = true;
		this.userService.get_employeesInfo().subscribe((resp : any[])=>{
			this.employess = resp;
		})
	this.loading = false;
	}
	//delete employee-details
	delEmployee(employee){
		this.emplyIndex = this.employess.indexOf(employee);
		if(confirm(this.emplyIndex)){
		//alert(this.emplyIndex);
		this.employess.splice(this.emplyIndex, 1);
		}	
    }
	
	//selected row when click the particukar row
	selectedRow(employee){
		this.showModal = true;
		this.row = employee
		this.rowindex = this.employess.indexOf(employee);		
	}
	
	//preRow
	preRow(){
		//console.log("this.rowindex first", this.rowindex);
		this.rowindex = (this.rowindex)-1;
		this.row = this.employess[this.rowindex];
		//console.log("this.rowindex after", this.rowindex);
	};
	
	//nextRow
	nextRow(){
		this.rowindex = (this.rowindex)+1;
		this.row = this.employess[this.rowindex];		
	}
	
	//show modal close function
	showModalClo(){
	    this.showModal = false;
	}





}
